.contentNews{
    display:grid;
    margin: 0 auto;
    width:60%;

}
.postOfNews{
    display: grid;
    width:60%;
    margin: 20px auto;
    padding:20px ;
    background-color: #F6F6F6;
    

}
.contentNews >h1 {
     margin: 20px auto;
}
@media screen and (max-width: 700px){
    .contentNews{
        width:80%;
    }
}
@media screen and (max-width: 1000px){
    .contentNews{
        width:100%;
    }
}